           :root {
              --clr-neon: hsl(317 100% 54%);
              --clr-bg: hsl(323 21% 16%);
          }
          
          .lofiVideo {
              width: 50%;
              height: 50vh;
          }
          
          .timeInput {
              height: 2rem;
              display: inline;
              margin-right: 0.3rem;
          }
          
          .submit-button {
              color: var(--clr-neon);
              font-size: large;
              display: inline-block;
              cursor: pointer;
              margin-left: 2px;
              border: var(--clr-neon) 0.125em solid;
              background: var(--clr-bg);
              padding: 0.25em 1em;
              border-radius: 0.25em;
              text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.5), 0 0 0.125em currentColor;
              box-shadow: inset 0 0 1em 0 var(--clr-neon), 0 0 1em 0 var(--clr-neon);
              position: relative;
          }
          
          .submit-button::before {
              content: "";
              position: absolute;
              background: var(--clr-neon);
              top: 120%;
              left: 0;
              width: 100%;
              height: 100%;
              transform: perspective(1em) rotateX(40deg) scale(1, 0.5);
              filter: blur(1em);
              opacity: 0.7;
          }
          
          .alarmsLink {
              color: white;
          }